<script>
      import { EmojiButton } from "@joeattardi/emoji-button";
export let sendMessage;
export let messageText = '';
export let updatingFormState;
export let tempMessageWrap;
export let tepmErrorState;
export let tepmError;

function showEmoji() {
    const picker = new EmojiButton({
      zIndex: 2,
      showSearch: false,
      theme: "dark",
    });
    picker.togglePicker(this);

    picker.on("emoji", (selection) => {
      // handle the selected emoji here
      messageText += selection.emoji;
    });
  }

</script>

<div class="chat__form">
    <form on:submit|preventDefault={sendMessage}>
      <input
        type="text"
        name="message"
        id="inputForm"
        bind:value={messageText}
        placeholder={!updatingFormState
          ? "Message #general"
          : "Sending..."}
      />
      <input type="submit" name="go" id="submit" />
      <div class="emoji-trigger" on:click={showEmoji}>
        <img src="https://uploads-ssl.webflow.com/623494ba6746d1d287d735b3/6253cec967bd41c07fec8137_Slightly-smiling-face.svg" alt="smile" />
      </div>
      {#if tepmErrorState}
        <div class="tepm__message" bind:this={tempMessageWrap}>
          <div class="tepm__message--text">{tepmError}</div>
        </div>
      {/if}
    </form>
  </div>

  <style>
      .chat__form {
    position: relative;
  }
  #inputForm {
    width: 100%;
    height: 48px;
    padding: 12px 14px;
    background: #2d0450;
    border-radius: 8px;
    border: none;
    color: white;
  }
  #inputForm::placeholder {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #c092ff;
  }
  #submit {
    cursor: pointer;
    position: absolute;
    width: 24px;
    height: 24px;
    right: 16px;
    top: 12px;
    color: transparent;
    background: url("https://uploads-ssl.webflow.com/623494ba6746d1d287d735b3/6253cec909453232cf289ae4_send.svg");
    border: none;
    margin: 0;
  }
  .emoji-trigger {
    position: absolute;
    right: 54px;
    top: 12px;
    cursor: pointer;
  }
  .tepm__message {
    width: 300px;
    border-radius: 14px 14px 8px 14px;
    background-color: gray;
    color: whitesmoke;
    padding: 12px;
    position: absolute;
    top: -50px;
    left: 0;
    transition: all ease 2.2s;
  }
  </style>