<script>
  import { doc, deleteDoc, updateDoc } from "firebase/firestore";
  import {clickOutside} from '../clickOutside.js';
  export let message;
  export let user;
  export let db;
  export let scrollToBottom;
  export let index;
  export let messages;

  let messageText = message.body;
  let updateState = false;
  let changeWrapeerState = false;

	function handleClickOutsideWrapper(event) {
		changeWrapeerState = false;
	}
  function handleClickOutsideInput(event) {
		updateState = false;
	}
  function handleWrapperState() {
		if(changeWrapeerState){
      changeWrapeerState = false
    }else{
      changeWrapeerState = true
    }
	}

  async function deleteMessage() {
    await deleteDoc(doc(db, "Messages", message.id));
    changeWrapeerState = false;
  }

  function updateMessageState() {
    updateState = true;
    changeWrapeerState = false;
    if(messages.length-1 === index){
      scrollToBottom()
    }
    

  }
  async function updateMessage() {
    const currentDoc = doc(db, "Messages", message.id);

    // Set the "capital" field of the city 'DC'
    await updateDoc(currentDoc, {
      body: messageText,
    });
    updateState = false;
    changeWrapeerState = false
  }
</script>

<li class:current={message.authorID === user.userToken} class="message--item" class:chenging={updateState} >
  <div class="user__data">
    <div class="user__name">{message.userName}</div>
    <div class="user__role">{message.userRole || "Passenger"}</div>
  </div>
  <div class="message__data">
    <div class="message__body">
      {#if updateState}
        <form on:submit|preventDefault={updateMessage} class="update__form" use:clickOutside on:click_outside={handleClickOutsideInput}>
          <div class="editable" contenteditable="true" bind:textContent={messageText}>{messageText}</div>
          <button type="submit"></button>
        </form>
      {:else if !updateState}
        {message.body}
      {/if}
    </div>
    <div class="message__time">
      {#if message.createdAt?.toDate}
        {`${message.createdAt.toDate().getHours()}:${
          message.createdAt.toDate().getMinutes() < 10
            ? "0" + message.createdAt.toDate().getMinutes()
            : message.createdAt.toDate().getMinutes()
        } ${message.createdAt.toDate().getHours() < 12 ? "am" : "pm"}`}
      {/if}
    </div>
  </div>
  {#if message.authorID === user.userToken}
    <div class="change__message--icon" on:click={handleWrapperState}>
      <div class="dot-icon" />
    </div>
    {#if changeWrapeerState}
    <div class="change__message--wrapper" use:clickOutside on:click_outside={handleClickOutsideWrapper}>
      <div class="delete_message" on:click={deleteMessage}>Delete</div>
      <div class="update_message" on:click={updateMessageState}>Update</div>
    </div>
    {/if}
    
  {/if}
</li>

<style>
  @import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap");

  .message--item {
    position: relative;
    font-style: normal;
    list-style: none;
    max-width: 340px;
    background: #59338c;
    border-radius: 14px 14px 14px 4px;
    padding: 10px;
    color: white;
    margin: 0 auto 8px 0;
    
  }
  .message--item.current {
    margin: 0 0 8px auto;
    border-radius: 14px 14px 4px 14px;
    background: #8f5dd3;
  }
  .message--item.current.chenging{
    max-width: 90%;
  }
 
  .delete_message:hover, .update_message:hover {
    transition: all ease 0.3s;
    opacity: 0.6;
  }
  .message--item.current .user__data {
    display: none;
  }
  .update__form {
    display: flex;
    max-width: 90%;
  }
  .editable{
    width: 100%;
    border: 1px solid #0000004a;
    padding: 10px;
    border-radius: 8px;
  }
  .update__form button{
    cursor: pointer;
    width: 30px;
    height: 30px;
    min-width: 30px;
    margin-left: 15px;
    background-color: gray;
    border: none;
    border-radius: 50%;
    background-image: url('https://uploads-ssl.webflow.com/623494ba6746d1d287d735b3/6253cff167bd4132ccec87ad_icons8-done.svg');
    background-repeat: no-repeat;
    background-size: 20px;
    background-position: center center;
    color: transparent;
    margin-top: auto;
    margin-bottom: 0;
  }
  .update__form button:hover{
    transition: all ease 0.3s;
    opacity: 0.6;
  }
  .user__data {
    display: flex;
    justify-content: space-between;
  }
  .user__name {
    color: #c8504f;
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
  }
  .user__role,
  .message__time {
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #ab8cd4;
  }
  
  .change__message--wrapper{
    width: 85px;
    background: #666666;
    padding: 12px;
    border-radius: 14px 14px 0px 14px;
    position: absolute;
    top: -64px;
    right: 10px;
  }
  .change__message--wrapper > *{ 
  }
  .update_message {
    cursor: pointer;
  }
  .delete_message {
    margin-bottom: 7px;
